<template>
  <div>
    <nav class="navbar navbar-light bg-white fixed-top c-nav-shadow">
      <router-link :to="{ name: 'Shop' }" class="navbar-brand">JKTV 線上影視租借</router-link>
      <ul class="navbar-nav px-3">
        <li class="nav-item text-nowrap">
          <a class="btn btn-outline-info" href="#" @click.prevent="signout">登出</a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>

export default {
  methods: {
    signout() {
      const url = `${process.env.VUE_APP_APIPATH}/logout`;
      const vm = this;
      vm.axios.post(url).then((response) => {
        if (response.data.success) {
          vm.$router.push({ name: 'Login' });
        }
      });
    },
  },
};
</script>
